export class TodoItem {
  text: String;
  img: String;
  title: String;
}

export class TodoStore {
  items: TodoItem[];

  constructor() {
    this.items = [];
  }

  addItem(newItem: String) {
    
    this.items.push({
      text: newItem,
      img: "https://ionicframework.com/dist/preview-app/www/assets/img/avatar-ts-woody.png",
      title: "Woody"
    });
  }
}


